.carrousel-container {
    width: 95%;
    margin: 0px auto 0 auto;
    position: relative;
    z-index: 10;
    @include respond-to (medium) {
     margin: 10px auto 0 auto; }
    .button-nav {
      width: 5%;
      cursor: pointer; } }

.owl-carousel {
    width: 90% !important;
    span {
      width: 100%;
      text-align: center;
      font-size: 19px; }
    .carrousel-item {
      background-color: #10549F;
      margin: 10px;
      padding: 20px;
      min-height: 377px;
      @include respond-to (medium) {
         min-height: 388px; }
      p {
        font-size: 14px;
        line-height: 15px;
        margin-top: 16px; }
      .image-cropper {
        height: 115px;
        width: 115px;
        margin: 0 auto; } } }
