.faq {
  .backgroundVideo {
     background: url("../../img/bogota-5.jpg") center no-repeat;
     background-size: cover; }
  .section-content {
     max-width: 800px;
     margin: 0 auto;
     color: #fff; }
  h1 {
     text-align: center;
     font-size: 2.5rem;
     color: white;
     font-weight: 400;
     width: 100%; }
  .section-content {
     z-index: 10;
     max-width: 920px; }
  .accordion-container {
    padding: 24px;
    background-color: rgba(59, 85, 119, 0.33);

    button.accordion.active {
      background-color: rgba(16, 205, 236, 0.41); }
    button.accordion.active:after {
      content: "\23EB"; }
    button.accordion.active:hover {
      background-color: rgba(16, 205, 236, 0.41); }

    button.accordion {
      padding: 13px;
      width: 100%;
      border: none;
      text-align: left;
      transition: 0.4s;
      background-color: rgba(42, 42, 42, 0.33);
      border-top: 2px solid rgb(255, 255, 255);
      cursor: pointer;
      color: rgb(255, 255, 255);
      font-size: 20px;
      transition: 0.4s; }

    .accordion:after {
      content: '\23EC';
      font-size: 27px;
      color: #fff;
      float: right;
      margin-left: 5px; }
    .accordion:hover {
      background-color: rgba(16, 205, 236, 0.41); }

    .panel p {
      margin: 10px 2px 10px 25px; }
    div.panel {
      max-height: 0;
      overflow: hidden;
      transition: 0.6s ease-in-out;
      opacity: 0; }
    div.panel.show {
      opacity: 1;
      max-height: 500px; } }

  .Services {
    display: none; }
  .BarNav {
     z-index: 11; }

  .backgroundVideo {
     z-index: 0; } }


