.Alert {
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.86);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Alert .Message {
  width: 18.75rem;
  background: #fafafa;
  padding: 1.25rem 2.5rem;
  box-sizing: border-box;
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.4);
  border-radius: 2px;
  text-align: center;
}

.Alert .Message a.close {
  float: right;
  text-transform: uppercase;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  height: 40px;
  text-align: right;
  color: #05347a;
}

.Alert .Message p {
  display: block;
  font-size: 1.1rem;
  line-height: 20px;
  font-weight: 400;
  margin: 24px 0 10px 0;
  color: #727272;
}

.Alert .Message h2 {
  font-size: 1.5rem;
  font-weight: 500;
  color: #212121;
}

.Alert.confirm .Message p {
  margin: 0 0 20px 0;
}

.Alert.confirm .Message a {
  justify-content: center;
  color: white;
  flex-grow: 1;
  text-align: center;
  background: #798ea1;
  margin: 0 2%;
  width: 46%;
}

.Alert.confirm .Message a:hover {
  background: #002444;
}

