.whoweare h1 {
  text-align: center;
  font-size: 1.8rem;
  color: white;
  font-weight: 400;
  width: 100%;
}

@media (min-width: 45rem) {
  .whoweare h1 {
    font-size: 2.5rem;
  }
}

.contact .contact-cols, .contactPost .contact-cols {
  margin-top: 0.5rem;
  width: 100%;
}

@media (min-width: 45rem) {
  .contact .contact-cols, .contactPost .contact-cols {
    margin-top: 3.5rem;
  }
}

.contact .contact-cols img, .contactPost .contact-cols img {
  width: 40px !important;
}

.contact .contact-cols span, .contactPost .contact-cols span {
  margin-left: 5px;
}

.contact .contact-cols div, .contactPost .contact-cols div {
  padding: 5px;
}

.contact .Services, .contactPost .Services {
  display: none;
}

.contact .backgroundVideo, .contactPost .backgroundVideo {
  background: url("../../img/bogota-5.jpg") center no-repeat;
  background-size: cover;
}

.contact .section-content, .contactPost .section-content {
  max-width: 800px;
  margin: 0 auto;
  margin-top: -100px;
  color: #fff;
  height: 99vh;
}

.contact h1, .contactPost h1 {
  margin-top: 6rem;
  text-align: center;
  font-size: 2.5rem;
  color: white;
  font-weight: 400;
  width: 100%;
}

@media (min-width: 45rem) {
  .contact h1, .contactPost h1 {
    text-align: center;
    margin-top: 8rem;
  }
}

.contact p, .contactPost p {
  font-size: 1.1rem;
  text-align: center;
  width: 100%;
}

.contact .BarNav, .contactPost .BarNav {
  z-index: 10;
}

.contact .formulario-contacto, .contactPost .formulario-contacto {
  padding: 40px 30px;
  background-color: #EFA53C;
}

.contact .item-contacto, .contactPost .item-contacto {
  margin-bottom: 5px;
}

.contact .item-contacto span, .contactPost .item-contacto span {
  padding: 5px;
  font-weight: 600;
}

.contact .error-message, .contactPost .error-message {
  width: 100%;
  background-color: #b95959;
}

.contact .success-message, .contactPost .success-message {
  width: 100%;
  background-color: #59b95a;
}

.contact .success-message p, .contactPost .success-message p {
  font-size: 1.3rem;
}

.contact .help-us .message, .contactPost .help-us .message {
  padding: 10px;
  margin-bottom: 30px;
}
